@import url('https://api.mapbox.com/mapbox-gl-js/v1.13.2/mapbox-gl.css');
h1,
p {
  font-family: Vazir, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  scroll-behavior: smooth;
  font-family: Vazir, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.map-wrapper {
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template: repeat(2, calc(50% - 0.5em)) / repeat(2, calc(50% - 0.5em));
  gap: 1em;
  border-radius: inherit;
  padding: 1em;
  overflow: hidden;
  box-sizing: border-box;
}
.map {
  width: 100%;
  height: 100%;
}

.title {
  position: absolute;
  top: 0;
  right: 0;
  background: rgb(255 255 255 / 60%);
  font-size: large;
  font-weight: bold;
  padding: 0.5em;
}

.section {
  position: relative;
}
